$main-accent: #060b35;
$secondary: #616070;
$blue-accent: #00acdc;
$font-nav: 12px;
$font-weight-nav: 500;
$star-size: 16px;
$card-space: 48px;
$card-width: 200px;
$row-leftRight-padding: 40px;
$logo-size: 40px;

$padding-topBottom-logo: ($logo-size / 4);

$line-height-navbar:
  ($logo-size + $padding-topBottom-logo * 2);

$section-width:
  ($row-leftRight-padding * 2 + $card-width * 4 + $card-space * 3);
